import React, { Component } from "react";
import ConnectButton from "../common/connectButton";
import Tags from "../common/tags";
import ShowMoreContent from "../common/showMoreContent";
import { SENTIMENTS_CALC } from "../../../../../utils";
import Media from "../common/media/Media";
import { MediaBaseUrl } from "../../../../../actions/api";
import NetworkIcon from "../common/networkIcon";
import AuthorInfo from "../../content/components/authorInfo";

class PostdetailPopup extends Component {
  render() {
    const {
      content,
      link,
      created,
      authorUsername,
      authorImage,
      network,
      postClicks,
      authorName,
      recommendationScore,
      postPerformance,
      sentiment,
      feedName,
      tags,
      networkId,
    } = this.props;

    return (
      <div class="influential_contributors_custom_modal px-5 py-5 sw_theme">
        <div className="influential-data-list-main">
          <div className="row">
            <div className="col-md-6">
              <div className="h-100">
                <Media item={this.props}/>
              </div>
            </div>
            <div className="col-md-6">
              <div className="influential-data-list mt-5">
                <div className="d-flex justify-content-between align-items-center">
                  <div className="d-flex align-items-center popup-author">
                    <AuthorInfo link={link} picture={authorImage} authorUsername={authorUsername} date={created} isIcon={network.icon} authorName={authorName}  networkId={networkId}/>
                  </div>
                  <div className="">
                    <NetworkIcon icon={network.icon} size={'font-size-h2'}/>
                  </div>
                </div>

              <div className="mt-5">
                <ShowMoreContent classes={'influ_content'} content={content} idIndex={0} line={3} />
              </div>
              

             { tags && Object.keys(tags).length ? 
              <div className="popup-tags-list mt-5">
                 <Tags tags={tags}/>
               </div>
                : null }
                
              <div className="popup-featur-list">
                <ul className="m-0">

                 {recommendationScore ?  
                  <li className="mt-5">
                    <p className="d-flex m-0" ><span className="w-50">AI Recommendation</span> <strong className="w-50 d-flex align-items-center"><b className="bulits bg-success d-block me-2 rounded-circle size-12"></b> Recommended ({recommendationScore}%)</strong></p>
                  </li>
                  : null }

                 { postPerformance ?
                  <li className="mt-5">
                    <p className="d-flex m-0"><span className="w-50">Impressions</span> <strong className="w-50">{postPerformance}</strong></p>
                  </li> : null }


                  { postClicks ?
                  <li className="mt-5">
                    <p className="d-flex m-0"><span className="w-50">Post Clicks</span> <strong className="w-50">{postClicks}</strong></p>
                  </li> : null }

                  { sentiment ?
                  <li className="mt-5">
                    <p className="d-flex"><span className="w-50">Sentiment</span> <strong className="w-50">
                    <img  className="img-fluid me-2"
                                    src={`${MediaBaseUrl}icons/emoji/emoji${
                                    SENTIMENTS_CALC(sentiment).item
                                    }.svg`}
                                    width={"16px"}
                                    height={"16px"}
                                />
                       {SENTIMENTS_CALC(sentiment).label} </strong></p>
                  </li> : null }

                  {feedName  ?
                  <li className="mt-5">
                    <p className="d-flex m-0"><span className="w-50">Feed Name</span> <strong className="w-50">#{feedName}</strong></p>
                  </li> : null }
                </ul>
              </div>
              <div className="conn_status connnected popup-connnected my-5">
                <ConnectButton link={link} network={network.id} />
              </div>
            </div>
          </div>
        </div>
      </div>
     </div> 
    );
  }
}

export default PostdetailPopup;
